@import "normalize";
@import "layout";
@import "elements";

html {
  font-size: 10px;
  background: url("/background.jpg");
  background-size: cover;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;

  .keys {
    display: flex;
    flex: 1;
    min-height: 100vh;
    align-items: center;
    justify-content: center;

    .key {
      border: 0.4rem solid black;
      border-radius: 0.5rem;
      margin: 1rem;
      padding: 1rem 0.5rem;
      transition: all 0.7s ease;
      width: 10rem;
      text-align: center;
      color: #fff;
      background: rgba(0, 0, 0, 0.4);
      text-shadow: 0 0 0.5rem black;
    }

    kbd {
      display: block;
      font-size: 4rem;
    }

    .sound {
      font-size: 1.2rem;
      text-transform: uppercase;
      letter-spacing: 0.1rem;
      color: #ffc600;
    }
  }

  .playing {
    transform: scale(1.1);
    border-color: #ffc600;
    box-shadow: 0 0 1rem #ffc600;
  }
}
